const builtInMarks = {
  bold: true,
  color: true,
  fonts: true,
  highlight: true,
  italic: true,
  strike: true,
  sz: true,
  underline: true,
  vertAlign: true,
  inlineCode: true,
  shd: true,
  spacing: true,
  kern: true,
  caps: true,
  smallCaps: true,
};

export default function isBuiltInMark(mark) {
  return builtInMarks[mark.type];
}
